import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false // 设置为 false 以阻止 vue 在启动时生成生产提示。

/* 资源引入 02 优化*/

//配置应用的字体比例
import './assets/js/font';//会经过优化
import './assets/css/base.css'; //全局暴露

//引入路由模块
import router from './plugins/router'

//引入axios插件配置模块
import axios from  './plugins/axios';

Vue.prototype.baseUrl = 'http://localhost:3000'//注册服务器的地址

//注册全局过滤器
// Vue.filter('过滤名',函数)
import date from './filters/date';
Vue.filter('date',date);

//引入动画库
import 'animate.css';


//引入状态管理配置模块
import store from './plugins/store'

//同步storage && state
let local = window.localStorage.getItem('vuex_user');
if(local){
  store.commit('UPDATE_USER',JSON.parse(local))
}

new Vue({
  render: h => h(App),
  router,store
}).$mount('#app')





