<template>
  <div id="app">
    <Loading v-show="bLoading"></Loading>
    <Header v-show="$store.state.bNav"></Header>

    <transition
      enter-active-class = "animated bounceInLeft delay-1s"
      leave-active-class = "animated bounceOutRight"
    >

      <keep-alive
        :exclude="['Detail','Column']"
      >
        <router-view></router-view>
      </keep-alive>

    </transition>
    
    
    <Footer v-show="bFoot"></Footer>
  </div>
</template>

<script>

import Header from './components/Header'
import Footer from './components/Footer'
import Loading from './components/Loading'

import { mapState } from 'vuex';
import { mapMutations } from 'vuex';

export default {
  name: 'app',
  watch:{
    $route:{
      handler({path}){
        // console.log('app',path)
        // $store状态管理，在vm实例身上,可以在this指向vm实例的时候，通过this.$store去使用store身上的方法

        if(/home|follow|column/.test(path)){
          this.$store.dispatch('VIEW_NAV',true)
          this.$store.dispatch('VIEW_FOOT',true)
        }
        if(/user/.test(path)){
          this.$store.dispatch('VIEW_NAV',false)
          this.$store.dispatch('VIEW_FOOT',true)
        }
        if(/login|reg|detail/.test(path)){
          this.$store.dispatch('VIEW_NAV',false)
          this.$store.dispatch('VIEW_FOOT',false)
        }

      },
      immediate:true
    }
  },
  components: {
    Loading,Header,Footer
  },
  computed:mapState(['bFoot','bLoading'])
  
}
</script>

<style>
.box{background:red}
</style>
