<template>
  <div class="home">
    <input type="button" value="按钮" @click="UPDATE_USER">
    <input type="button" value="按钮2" @click="show">
    <Slider :banner="banner"></Slider>
    <List :list="home" dataName="home" ></List>
  </div>
</template>
<script>
// import Slider from '../components/Slider'
import Slider from '@/components/Slider'
import List from '@/components/List'

import { mapGetters, mapActions, mapMutations } from 'vuex';
import * as types from '../store/types.js'
export default {
  name:'home',

  components:{Slider,List},

  activated(){
    this.$store.dispatch('UPDATE_HOME');
    this.$store.dispatch('UPDATE_BANNER');
  },

  computed:mapGetters([
    'home','banner'
  ]),

  /* methods:{
    UPDATE_USER(){
      this.$store.commit('jia',12)
    },
    UPDATE_USER(){
      this.$store.dispatch('UPDATE_USER',23)
    },
  }, */
  
  methods:{

    ...mapMutations([
      types.UPDATE_USER
    ]),

    ...mapActions([
      types.UPDATE_USER
    ]),

    show(){alert('show')}
    
  }
  

}
</script>

