<template>
  <div class="error">
    <h3>结算</h3>
    <h3>结算</h3>
    <h3>结算</h3>
    <h3>结算</h3>
    <h3>结算</h3>
    <h3>结算</h3>

    <li
      v-for="(item,index) of buycar"
      :key="item._id"
    >
      {{item}}/{{index}}
      <a @click="changeItem({_id:item._id,num:1})">+</a>
      <a @click="changeItem({_id:item._id,num:-1})">-</a>
      <a href="javascript:;" @click="removeItem({_id:item._id})">删除商品</a>
    </li>

    <button @click="clearBuycar" v-show="buycar.length>0">清空</button>



  </div>
</template>
<script>
import {mapGetters,mapActions} from 'vuex'
export default {
  computed:mapGetters(['buycar']),
  methods:mapActions([
    'changeItem','clearBuycar','removeItem'
  ])
}
</script>

